@import url('https://fonts.googleapis.com/css?family=Spartan:300,400,700&display=swap');

html, .uk-h1, .uk-h2, .uk-h3, .uk-h4, .uk-h5, .uk-h6, .uk-heading-2xlarge, .uk-heading-large, .uk-heading-medium, .uk-heading-small, .uk-heading-xlarge, h1, h2, h3, h4, h5, h6{
    font-family: 'Spartan', sans-serif;
    color: rgb(49, 49, 49);
}

a:link, a:visited, a:active {
    text-decoration:none;
}

.uk-text-meta {
    font-size: .7rem;
}

.data-table-extensions {
    padding: 24px !important;
}

.uk-button{
    font-size: 11px;
    padding: 0 15px;
}

.uk-card-primary.uk-card-body .uk-text-emphasis, .uk-card-primary>:not([class*=uk-card-media]) .uk-text-emphasis, .uk-card-secondary.uk-card-body .uk-text-emphasis, .uk-card-secondary>:not([class*=uk-card-media]) .uk-text-emphasis, .uk-light .uk-text-emphasis, .uk-offcanvas-bar .uk-text-emphasis, .uk-overlay-primary .uk-text-emphasis, .uk-section-primary:not(.uk-preserve-color) .uk-text-emphasis, .uk-section-secondary:not(.uk-preserve-color) .uk-text-emphasis, .uk-tile-primary:not(.uk-preserve-color) .uk-text-emphasis, .uk-tile-secondary:not(.uk-preserve-color) .uk-text-emphasis{
    color: rgb(49, 49, 49);
}

.uk-nav-primary>li>a{
    font-size: 16px;
}

.uk-spinner {
    color: #1e87f0;
}

.uk-label {
    padding: 7px 5px;
    background: transparent;
}

.uk-card-badge {
    top: 10px;
    right: 10px;
}

.uk-search-navbar .uk-search-input {
    font-size: 14px;
}